<template>
    <div class="singleblog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <p>分类</p>
        <ul>
            <li v-for="item in blog.categories">{{item}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'SingleBlog',
    data:function(){
        return {
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        this.$http.get('https://myblog-ce34e.firebaseio.com/post/' + this.id + ".json")
            .then(data=>{
                console.log(data)
                this.blog = data.body;
            })
    }
}
</script>
<style scoped>
.singleblog{
    max-width:960px;
    margin:0 auto;
    padding:20px;
    background: #eee;
    border:1px dotted #aaa;
}
</style>




